import {
	addRasumeProject,
} from '@/api/job'
import {
	$wuxToast,
} from '../../../wxcomponents/wux/index'
export default {
	data() {
		return {
			to: 1,
			ready: false,
			options: {
				start: 1573026683000,
				end: 1573026683000,
			},
			show: {
				start: false,
				end: false,
			},
			form: {
				start: null,
				end: null,
				company: null,
				name: null,
				description: null,
			},
		}
	},
	onReady() {
		this.ready = true
	},
	onLoad(e) {
		if (e.list) {
			this.to = e.list
		}
	},
	methods: {
		onChange(e) {
			let f = this.form
			let i = e.target.id
			let v = e.detail
			const reg = /\uD83C[\uDF00-\uDFFF]|\uD83D[\uDC00-\uDE4F\uDE80-\uDEFF]/g
			if (i === "company") {
				f.company = v.replace(reg, '')
			} else if (i === "name") {
				f.name = v.replace(reg, '')
			} else if (i === "description") {
				f.description = v.replace(reg, '')
			}
		},
		onOptionsChange(e) {
			const i = e.target.id
			const d = e.detail
			const f = this.form
			const s = this.show
			if (i === 'start') {
				f.start = d / 1000
				s.start = false
			} else if (i === 'end') {
				f.end = d / 1000
				s.end = false
			}
		},
		onPicker(n) {
			const s = this.show
			if (n === 'start') {
				s.start = true
				console.log(n)
			} else if (n === 'end') {
				s.end = true
			}
		},
		onSubmit() {
			setTimeout(() => {
				const f = this.form
				if (!f.start) {
					this.forbiddenToast("开始时间未选")
					return false
				} else if (!f.end) {
					this.forbiddenToast("结束时间未选")
					return false
				} else if (!f.company) {
					this.forbiddenToast("公司未填写")
					return false
				} else if (!f.name) {
					this.forbiddenToast("项目名称未填")
					return false
				} else if (!f.description) {
					this.forbiddenToast("工作描述未填")
					return false
				}
				const form = Object.assign({},
					f.start ? {
						start: f.start
					} : {},
					f.end ? {
						end: f.end
					} : {},
					f.company ? {
						company: f.company
					} : {},
					f.name ? {
						name: f.name
					} : {},
					f.description ? {
						description: f.description
					} : {},
				);
				addRasumeProject(form).then(response => {
					let r = response.data
					if (response.code === 20000) {
						if (r.add) {
							$wuxToast().show({
								type: 'success',
								text: '新增成功'
							})
							setTimeout(() => {
								uni.navigateBack();
							}, 1000)
						} else {
							$wuxToast().show({
								type: 'forbidden',
								text: '保存失败'
							})
						}
					} else {
						$wuxToast().show({
							type: 'forbidden',
							text: '请求异常'
						})
					}
				})
			}, 500)
		},
		forbiddenToast(text) {
			$wuxToast().show({
				type: 'forbidden',
				text: text
			})
		},
		Back() {
			if (this.to > 1) {
				uni.navigateBack({
					delta: 2
				});
			} else {
				uni.navigateBack();
			}
		}
	}
}
